@import "main";

/* Container holding the image and the text */
.container {
    position: relative;
    text-align: center;
    color: white;
  }
  
/* Centered text */
.bottom-center {
  position: absolute;
  bottom: 0;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  width: 80%;
  background-color: rgba(65, 65, 187,.5);
}

.top-center {
  position: absolute;
  top: 5vw;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  width: 100%;
  background-color: rgba(65, 65, 187,.5);
}